@import url(https://fonts.googleapis.com/css2?family=Prompt:ital,wght@0,600;1,900&family=Quicksand:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Importing the Quicksand font thought the Google Font API */

:root {
    --cobalt-blue: #1968f1;
    --nitrogen-blue: #134fb6;
    --carbon-gray: #4d4d4d;

    --silver-sheen: linear-gradient(225deg, #fff 0%, #efefefff 80%, #f3f3f3ff 100%);
    --iodine-vapor: linear-gradient(225deg, #1d7fff 0%, rgb(79, 16, 250) 80%, rgb(93, 39, 219) 100%);
}

* {
    font-family: 'Quicksand', Helvetica, sans-serif;
}

body {
    background: linear-gradient(225deg, rgb(211, 245, 255) 0%, rgb(165, 229, 255) 80%, rgb(142, 232, 255) 100%);
    margin: 0;
    min-height: 100vh;
}

.hidden {
    display: none;
}

.visible {
    display: contents;
}

/* ---Welcome Banner Styling--- */
.welcome_banner {
    background-color: #fff;
    box-shadow: 0px 1px 5px 0px rgba(131, 131, 131, 0.9);

    margin: 1vh auto;

    border-radius: 25px;
    padding: 10px;
    width: 50%;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
}

.welcome_banner h1 {
    text-align: center;
    font-size: xx-large;
    color: #1968f1;
    color: var(--cobalt-blue);
}

.welcome_banner p {
    background: linear-gradient(225deg, #1d7fff 0%, rgb(79, 16, 250) 80%, rgb(93, 39, 219) 100%);
    background: var(--iodine-vapor);
    color: #fff;
    font-weight: bold;

    padding: 10px;
    width: 80%;
    margin: 0 auto;
}

.welcome_banner .explanation {
    padding: 0 5%;
}

.explanation h3 {
    margin-bottom: 0;
}

.explanation ol {
    margin-top: 0;
}

.welcome_banner em {
    color: #1968f1;
    color: var(--cobalt-blue);
    font-weight: bold;
}

.welcome_banner aside {
    border-radius: 15px;
    background: linear-gradient(225deg, #fff 0%, #efefefff 80%, #f3f3f3ff 100%);
    background: var(--silver-sheen);
    padding: 10px;
}

/* ---General Form Styling - shared between multiple elements--- */
.form_container {
    background-color: #fff;
    margin: 2vh auto;
    width: 40%;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    /* max-width: max-content; */

    box-shadow: 0px 1px 5px 0px rgba(131, 131, 131, 0.9);
}

form {
    padding: 0;
}

/* Silver Box Styling */
.ag_box {
    background: linear-gradient(225deg, #fff 0%, #efefefff 80%, #f3f3f3ff 100%);
    background: var(--silver-sheen);
    padding: 10px;
}

.ag_box h1, h3.ag_box_dyn_heading {
    color: #4d4d4d;
    color: var(--carbon-gray);
}

/* Input Styling */
.input_block {
    display: inline;
}

label {
    color: #1968f1;
    color: var(--cobalt-blue);
}

input {
    background-color: #fff;
    color: #1968f1;
    color: var(--cobalt-blue);
    border: unset;
    outline: 1px solid #1968f1;
    outline: 1px solid var(--cobalt-blue);
    border-radius: 5px;

    margin: 1rem auto;
    padding: 4px;
}

input:hover {
    outline: 1px solid #134fb6;
    outline: 1px solid var(--nitrogen-blue);
    box-shadow: inset 0px 1px 5px 0px rgba(131, 131, 131, 0.1);
}

input:focus {
    outline: 1px solid #111;
}

input:disabled {
    cursor: not-allowed;
    outline: 1px solid #4d4d4d;
    outline: 1px solid var(--carbon-gray);
}

input:disabled:hover {
    box-shadow: unset;
}

/* ---Error Message Styling--- */
.error_container {
    background-color: rgb(230, 140, 137);
    border: 1px solid crimson;
    border-radius: 5px;
    padding: 10px;

    max-width: -webkit-fit-content;

    max-width: -moz-fit-content;

    max-width: fit-content;
    width: 60%;

    margin: 8px auto;
}

.error_msg {
    color: #fff;
    font-weight: bold;
}

/* ---Initial Data Entry Styling--- */
/* #initial_data_entry {
    padding-bottom: 10px;
} */

.percent_type {
    font-size: 1.5rem;
    margin: 0 15px;
}

#wpercent, #mpercent {
    outline: unset;
}

.optional {
    margin: 0 auto 0 10px;
}

#funcA_eq, #funcB_eq {
    display: inline;
}

/* ---Monomer (Dynamic) Data Entry Styling--- */
/* .dynamic_form {
    padding-top: 10px;
    padding-bottom: 10px;
} */

.dyn_heading {
    background: linear-gradient(225deg, #1d7fff 0%, rgb(79, 16, 250) 80%, rgb(93, 39, 219) 100%);
    background: var(--iodine-vapor);
    color: #fff;

    margin: 0;
    padding: 5px;
}

.dynamic_form label {
    margin-right: 5px;
    margin-left: 5px;
}

/* ---Final Results Styling--- */
.final_results .group_heading {
    background: linear-gradient(225deg, #42474d 0%, rgb(35, 22, 71) 80%, rgb(19, 6, 49) 100%);
    color: #fff;

    margin: 0;
    padding: 10px 0 5px 15px;
}

th {
    padding: 6px;
}

td {
    text-align: center;

    background-color: #fff;
    padding: 5px;
    border-radius: 5px;
}

/* ---Button Styling--- */
button {cursor: pointer;}

/* Custom Checkbox Styling - not a standard HTML checkbox */
.check_box {
    border: unset;
    background-color: #fff;
    outline: 2px solid #1968f1;
    outline: 2px solid var(--cobalt-blue);

    width: 20px;
    height: 20px;
    margin-left: 10px;
}

.checked {
    background-color: #1968f1;
    background-color: var(--cobalt-blue);
    outline: 2px solid #134fb6;
    outline: 2px solid var(--nitrogen-blue);
}

.check_box:hover {
    outline: 2px solid #134fb6;
    outline: 2px solid var(--nitrogen-blue);
    box-shadow: inset 0px 1px 5px 0px rgba(131, 131, 131, 0.1);
}

/* Square Button Styling - Excess functional group selectors and increment/decrement buttons */
.square_button {
    display:inline;
    border-radius: 5px;

    font-weight: bold;

    width: 32px;
    height: 32px;
    padding: 8px;
}

.unselected .square_button, .square_button.plus_minus {
    background-color: #fff;
    color: #1968f1;
    color: var(--cobalt-blue);
    border: 1px solid #1968f1;
    border: 1px solid var(--cobalt-blue);
}

.square_button.plus_minus {
    padding: 0;
    font-size: large;
}

.selected .square_button {
    background-color: #1d7fff;
    color: #fff;
    border: 1px solid #1968f1;
    border: 1px solid var(--cobalt-blue);
}

.square_button:hover {
    border: 1px solid #134fb6;
    border: 1px solid var(--nitrogen-blue);
    -webkit-transform: scale(1.075);
            transform: scale(1.075);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.05), 0 6px 20px 0 rgba(0, 0, 0, 0.01);
}

.square_button:active {
    border: 1px solid #4d4d4d;
    border: 1px solid var(--carbon-gray);
    -webkit-transform: scale(0.975);
            transform: scale(0.975);
    box-shadow: inset 0 4px 8px 0 rgba(0, 0, 0, 0.05), 0 6px 20px 0 rgba(0, 0, 0, 0.01);
}

/* Submit Button Styling */
.submit_container {
    padding: 10px;
}

.submit_button {
    font-size: large;
    color: #4d4d4d;
    color: var(--carbon-gray);
    font-style: italic;
    font-weight: bold;

    display: block;
    padding: 8px 16px;
    margin: auto 0px 0px auto;

    background-color: yellow;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.10);
    border: unset;
    border-radius: 5px;
}

.submit_button:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/***********************************************************************************************************************/

/* ---Mobile styling (phones, tablets)--- */
@media screen
and (min-device-width: 320px)
and (max-device-width: 960px)
and (-webkit-min-device-pixel-ratio: 2) {
    * {
        font-size: 2vh;
    }

    .welcome_banner {
        padding: 30px;
    }

    .welcome_banner p, 
    .welcome_banner li, 
    .welcome_banner em, 
    .welcome_banner aside {
        font-size: 12px;
    }

    .form_container, .welcome_banner {
        width: 90%;
    }

    .percent_type label {
        font-size: 3vh;
    }

    .input_block {
        margin: 0 auto;
    }

    .input_block input {
        margin: 0;
        display: block;
    }

    .dynamic_form label,
    .dynamic_form input {
        display: block;
    }

    .dynamic_form label, .dynamic_form h3 {
        text-align: center;
    }
    
    td, th {
        padding: 0;
        border-radius: 15px;
    }

    .square_button {
        width: 3vh;
        height: 3vh;
    }

    .check_box {
        width: 40px;
        height: 40px;
    }

    .submit_button {
        font-size: 2vh;
    
        /* display: block;
        padding: 8px 16px;
        margin: auto 0px 0px auto; */
    }

}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

